// scss-lint:disable ColorVariable
html {
  font-size: 62.5%;
}

body {
  background: #fff;
  color: #000;
  font-size: 1rem;
  font-smoothing: antialiased;
  line-height: 1.33;
  text-rendering: optimizeLegibility;
}

h1,
h2,
h3,
h4,
h5,
h6,
ol,
ul,
p {
  margin: 0;
}

h1,
h2,
h3,
h4 {
  font-weight: normal;
  margin-bottom: $margin-default / 2;
}

h3,
h4 {
  text-transform: uppercase;
}

p,
li {
  @include set-font-size(1.5rem, 1.5rem, 1.5rem);
}

h1 {
  @include set-font-size(3rem, 4rem, 6rem);
}

h2 {
  @include set-font-size(2rem, 3rem, 4rem);
}

h3 {
  @include set-font-size(2rem, 2.5rem, 3rem);
}

h4 {
  @include set-font-size(2rem, 2rem, 2rem);
}

blockquote {
  @include set-font-size(2rem, 2rem, 2rem);
  margin: 0;
}
