html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

* {
  ::selection {
    background: $primary-font-color;
    color: $white;
    text-shadow: none;
  }
}

img {
  max-width: 100%;
}

ul,
ol {
  padding-left: 0;
}

h1,
h2,
h3,
h4,
ol,
ul {
  margin-bottom: 0;
}

hr {
  border-color: $grey;
  margin: $margin-default 0;
}

figure {
  margin: 0;
}

blockquote {
  .quote {
    quotes: '“' '”' '‘' '’';

    &::before {
      content: open-quote;
    }

    &::after {
      content: close-quote;
    }
  }
}

.logo {
  max-width: 2.4rem;
}

a {
  text-decoration: none;
}
